
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
components: {
Cell,
Group,
XTextarea,
XInput,
Datetime,
XAddress,
PopupPicker
},
data () {
return {
birthday: '',
name: 'Canny',
address: [],
addressData: ChinaAddressV4Data,
sexList: [['男', '女']],
sex: ['男']
}
},
methods: {
updateName () {
const _this = this
this.$vux.confirm.show({
title: '修改昵称',
showInput: true,
onShow () {
_this.$vux.confirm.setInputValue(_this.name)
},
onConfirm (value) {
_this.name = value
}
})
}
}
}



